import React from 'react'
import styled from 'styled-components'

import { CanMutate } from '../../../atoms/CanMutate'
import { Card } from '../../../atoms/Card'
import { useRoot } from '../../../atoms/RootProvider'
import { ScrollArea } from '../../../atoms/ScrollArea'
import { UserHasMultipleBranches } from '../../../atoms/UserHasMultipleBranches'
import { useAutorunWhenSingleBranch } from '../../../hooks/useAutorunWhenSingleBranch'
import { CreateZoneButton } from '../molecules/CreateZoneButton'
import { ZoneListControl } from '../organisms/ZoneListControl'
import { ZoneFilter } from './ZoneFilter'

const ZoneMenuContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
`

const FixedHeaderContainer = styled.div`
  flex: 0 0 auto;
`
const ScrollAreaContainer = styled.div`
  flex: 1 1 0;
  min-height: 0;
`
export const ZoneMenuPanel = React.memo(() => {
  const { zone } = useRoot()
  useAutorunWhenSingleBranch(branch => {
    zone.setBranchFilter(branch)
  })
  return (
    <ZoneMenuContainer>
      <FixedHeaderContainer>
        <UserHasMultipleBranches>
          <Card>
            <ZoneFilter />
          </Card>
        </UserHasMultipleBranches>
        <CanMutate mutationName="createZone">
          <CreateZoneButton />
        </CanMutate>
      </FixedHeaderContainer>

      <ScrollAreaContainer>
        <ScrollArea>
          <ZoneListControl />
        </ScrollArea>
      </ScrollAreaContainer>
    </ZoneMenuContainer>
  )
})
