import { Polygon } from '@react-google-maps/api'
import React from 'react'

import { gpsToLatLng, latLngToGps } from '../../../helpers/gps'

interface IZoneMapAreaProps {
  points: ReadonlyArray<GeoPoint>
  color: string
  priority: number
  onChange(points: GeoPoint[]): void
  onClear(): void
}

export const ZoneMapEditableArea: React.FC<IZoneMapAreaProps> = props => {
  const polygonRef = React.useRef<google.maps.Polygon | null>(null)
  const listenersRef = React.useRef<google.maps.MapsEventListener[]>([])

  const onAreaChange = React.useCallback(() => {
    if (polygonRef.current) {
      const path = polygonRef.current.getPath()
      props.onChange(path.getArray().map(latLngToGps))
    }
  }, [props.onChange])

  const onLoad = React.useCallback((polygon: google.maps.Polygon) => {
    polygonRef.current = polygon
    const path = polygon.getPath()
    listenersRef.current.push(
      path.addListener('set_at', onAreaChange),
      path.addListener('insert_at', onAreaChange),
      path.addListener('remove_at', onAreaChange),
    )
  }, [])

  const onUnmount = React.useCallback(() => {
    props.onClear()
    listenersRef.current.forEach(lis => lis.remove())
    polygonRef.current = null
  }, [])

  const { points, color } = props
  return (
    <Polygon
      path={points.map(gpsToLatLng)}
      options={{
        fillColor: color,
        fillOpacity: 0.6,
        strokeWeight: 2,
        zIndex: 10000,
      }}
      editable={true}
      draggable={true}
      onDragEnd={onAreaChange}
      onLoad={onLoad}
      onUnmount={onUnmount}
    />
  )
}
